<template>
  <v-app>
    <v-content>
      <v-container class="fill-height" fluid>
        <v-row align="center" justify="center">
          <v-col cols="12" sm="10" md="8" lg="6">
            <v-card>
              <v-form ref='form' @submit.prevent="login">
                <v-toolbar dark flat color="primary">
                  <v-toolbar-title>Sign In</v-toolbar-title>
                </v-toolbar>
                <v-card-text>
                  <v-text-field
                    :rules="[rules.requires]"
                    v-model="form.data.username"
                    label="Username"
                    prepend-icon="fa-user"
                  />
                  <v-text-field
                    :type="form.control.password_display?'text':'password'"
                    :rules="[rules.requires]" 
                    v-model="form.data.password"
                    label="Password" @click:append='change_password_display'
                    prepend-icon="fa-lock" :append-icon="`fa-${form.control.password_display?'eye':'eye-slash'}`"
                  />
                </v-card-text>
                <v-card-actions>
                  <v-btn color="primary[" @click="login">Login</v-btn>
                  <v-btn color="secondary" to="join">Join</v-btn>
                  <v-spacer />
                  <v-btn text color="secondary">forget password</v-btn>
                </v-card-actions>
              </v-form>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
  </v-app>
</template>



<script>
import img0 from "../assets/bg1.jpg";
import {rules} from '../plugins/rules'
import { log } from 'util';

export default {
  data() {
    return {
      bg0: img0,
      route: {
        state: 0,
        message: "",
        next: ""
      },
      type: 0,
      snackbar: {
        text: "",
        color: "success",
        value: false
      },
      continue_as_form: {
        control: {
          password_display: false,
          loading: false,
          remember: true,
          show: false
        },
        data: { email: "", password: "" },
        error: { message: "" }
      },
      form: {
        control: {
          password_display: false,
          loading: false,
          remember: false
        },
        data: {
          username: "",
          password: ""
        },
        error: {
          message: ""
        }
      },
      rules:rules
    };
  },
  computed: {},
  methods: {
    change_password_display() {
      this.form.control.password_display = !this.form.control.password_display
    },
    async login() {
      var self = this;
      if (self.form.control.loading) {
        return false;
      }
      self.form.error.message = "";
      if (self.$refs.form.validate()) {
        try {
          let response = await this.axios.post("/user/auth/login", this.form.data)
          this.$store.dispatch('login',response.data)
          this.$router.replace({path:'/account'})
        } catch (error) {
          self.snackbar.value = true;
            self.snackbar.color = "error";
            self.snackbar.text =
              "Login failed due to connection error. please try again in a while";
        }
        this.form.control.loading = false
      } else {
        self.form.error.message = "Please input all fields";
      }
    }
  },
  mounted() {
  },
  props: {
    display: {
      default: 0
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      if (to) {
        vm.route.state = 1;
        vm.route.message = to.params.message;
        vm.route.next = to.query.next;
      }
    });
  }
};
</script>
